.app-menu .navbar-nav .nav-link {
    color: #000 !important;
}

.app-menu .navbar-nav .nav-link.active {
    background-color: rgba(255, 255, 255, 0.3) !important;
    border-radius: 6px;
}

.app-menu {
    background-color: #7a9bc7 !important;
}

.app-menu .menu-title span {
    color: black !important;
    font-weight: 700;
    text-transform: uppercase;
}

/* ocultar textos al colapsar (animado) */
body.vertical-collapsed .navbar-nav .menu-link span {
    opacity: 0;
    width: 0;
    overflow: hidden;
    transition: opacity 0.2s ease, width 0.2s ease;
}

.navbar-nav .menu-link span {
    opacity: 1;
    width: auto;
    transition: opacity 0.2s ease, width 0.2s ease;
}

/* ========= Sidebar ========= */
.app-menu.navbar-menu {
    width: 250px;
    transition: width 0.3s ease, transform 0.3s ease;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 1000;
    overflow: hidden;
}

/* collapsed */
body.vertical-collapsed .app-menu.navbar-menu {
    width: 70px;
}

/* ========= Header - CORREGIDO Z-INDEX ========= */
#page-topbar {
    position: fixed !important;
    top: 0;
    left: 250px;
    right: 0;
    width: calc(100% - 250px);
    z-index: 1050 !important; /* Reducido para que esté debajo del modal */
    transition: left 0.3s ease, width 0.3s ease;
    background: transparent !important;
    box-shadow: none !important;
    border: none !important;
}

body.vertical-collapsed #page-topbar {
    left: 70px !important;
    width: calc(100% - 70px) !important;
}

body.vertical-collapsed #page-topbar .layout-width {
    margin-left: 0;
}

/* ========= Main content ========= */
.main-content {
    margin-left: 250px;
    padding-top: 0 !important;
    transition: margin-left 0.3s ease;
}

body.vertical-collapsed .main-content {
    margin-left: 70px;
}

body.vertical-collapsed .page-title-box,
body.vertical-collapsed .page-content {
    margin-left: 1;
}

/* ========= Botón hamburguesa ========= */
#topnav-hamburger-icon {
    transition: transform 0.3s ease;
}

/* ========= Overlay y responsive ========= */
@media (max-width: 991px) {
    .app-menu.navbar-menu {
        transform: translateX(-100%);
        left: 0;
        width: 250px;
    }

    body.vertical-sidebar-enable .app-menu.navbar-menu {
        transform: translateX(0);
    }

    #page-topbar {
        left: 0 !important;
        width: 100% !important;
    }

    .main-content {
        margin-left: 0 !important;
    }
}

.vertical-overlay {
    display: none !important;
}

/* ========= Footer ========= */
.footer {
    background: white;
    padding: 10px 20px;
    position: fixed;
    bottom: 0;
    left: 250px;
    width: calc(100% - 250px);
    transition: left 0.3s ease, width 0.3s ease;
    z-index: 999;
    border-top: 1px solid #ddd;
}

body.vertical-collapsed .footer {
    left: 70px;
    width: calc(100% - 70px);
}

@media (max-width: 991px) {
    .footer {
        left: 0 !important;
        width: 100% !important;
    }
}

/* ========= Header Styles - Fondo transparente ========= */
#page-topbar .navbar-header,
#page-topbar .layout-width {
    background: transparent !important;
    box-shadow: none !important;
}

#page-topbar .dropdown,
#page-topbar .dropdown .dropdown-toggle,
#page-topbar .dropdown-menu,
#page-topbar .navbar-header .user-dropdown {
    background: transparent !important;
    box-shadow: none !important;
    border: none !important;
}

/* Texto del usuario en el header (Admin / Administrador) */
#page-topbar .navbar-header .user-dropdown,
#page-topbar .navbar-header .user-dropdown span,
#page-topbar .navbar-header .user-name,
#page-topbar .navbar-header .user-role {
    color: #000 !important;
    font-weight: 600;
    opacity: 1 !important;
    visibility: visible !important;
}

/* ===== CORRECCIÓN COMPLETA DEL AVATAR ===== */
/* Fuerza la visibilidad del avatar SIEMPRE */
#page-topbar img,
#page-topbar .navbar-header img,
#page-topbar .user-dropdown img,
#page-topbar .dropdown-toggle img,
#page-topbar .avatar img,
#page-topbar .user-avatar img {
    opacity: 1 !important;
    visibility: visible !important;
    display: inline-block !important;
    position: relative !important;
    z-index: 1051 !important; /* Más alto que el header pero menos que el modal */
    border: none !important;
    box-shadow: none !important;
    background: none !important; /* Cambiado de transparent a none */
    background-color: transparent !important;
}

/* Para cualquier contenedor del avatar */
#page-topbar .navbar-header,
#page-topbar .navbar-header .dropdown,
#page-topbar .navbar-header .user-dropdown,
#page-topbar .navbar-header .dropdown-toggle,
#page-topbar .user-wrapper,
#page-topbar .avatar-wrapper {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
}

/* ===== MODALES CON Z-INDEX CORRECTO ===== */
.modal-overlay {
    z-index: 10000 !important; /* Más alto que todo */
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: transparent !important;
    border-radius: 8px;
    width: 90%;
    max-width: 800px;
    max-height: 90vh;
    overflow-y: auto;
    z-index: 10001 !important; /* Más alto que el overlay */
}

.form-container {
    padding: 30px;
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 10002 !important;
}

/* ===== SOLUCIÓN ADICIONAL - Si aún no funciona ===== */
/* Fuerza la imagen del avatar en cualquier contexto */
body img[src*="avatar"],
body img[alt*="avatar"],
body img[alt*="user"],
body img[class*="avatar"],
body img[class*="user"] {
    opacity: 1 !important;
    visibility: visible !important;
    display: inline-block !important;
}

/* Evita que cualquier overlay afecte las imágenes del header */
#page-topbar * {
    position: relative;
    z-index: 1051;
}

#page-topbar img {
    z-index: 1052 !important;
}